@import 'reset';
@import 'variables';

// UTILITIES
.container {
    padding: 2rem;
    margin: 0.25rem auto;
    overflow: hidden;
  }

@mixin flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

// MAIN

header {
    background: $primary-color;
    font-family: $font-stack-s;
}

main {
    background-color: $light-color;
    font-family: $font-stack;

    // MAIN NAV
    #navbar-main-list {
       display: flex;
       align-items: center;
       justify-content: flex-start;

       li:nth-child(3) {
           flex-grow: 1;
       }
        
    }
    
    #navbar-secondary-list {
        li {
            display: flex;
            // TOGGLER
            .toggler-container {
                display: flex;

                input {
                    display: none;
                    opacity: 0;
                }

                .toggle {
                    font-size: 20px;
                    width: 2.4rem;
                    background-color: lightgray;
                    border-radius: 1rem;
                }

                .circle {
                    height: 1.2rem;
                    width: 1.2rem;
                    border-radius: 50%;
                    background-color: #fff;
                    transition: all 300ms;
                }

                .toggle input:checked + div {
                    transform: translateX(100%);
                    background-color: green;
                }
        }
        }
    }
}
